.products__upper-title {
  margin-bottom: 15px;
  @media screen and (min-width: 768px) {
    font-weight: 400;
  }
}
.products__title {
  margin-bottom: 140px;
  @media screen and (min-width: 768px) {

    margin-bottom: 158px;
    font-weight: 400;
  }
    @media screen and (min-width: 1200px){
      margin-bottom: 281px;
    }}

.products__list {
  @media screen and (max-width: 767px) {
      display: flex;
        flex-direction: column;
        align-items: center;
  }
  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
  }
}
.products__item:not(:last-child) {
  @media screen and (max-width: 767px) {
    margin-bottom: 143px;
  }
}
.products__item {
  @media screen and (min-width: 768px) {
    &:not(:first-child) {
      margin-left: 16px;
    }
  }
  @media screen and (min-width: 1200px) {
    &:not(:first-child) {
      margin-left: 32px;
    }
  }
}
.card-contents {
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 40px;
  text-align: center;
  position: relative;
  width: 280px;

    // height: 489px;
  padding-bottom: 40px;

  background-color: #ffb8ca;
  border-radius: 24px;
  &--yellow {
    background-color: #f0d1a5;
  }
  &--green {
    background-color: #c2e297;
  }
  @media screen and (min-width: 768px) {
    padding-right: 32px;
    padding-left: 32px;

    width: 224px;
  }
  @media screen and (min-width: 1200px) {
    padding-right: 62px;
    padding-left: 62px;
    padding-bottom: 60px;
     width: 368px;
    }
}

.products__image {
  position: absolute;
  top: -28%;
  left: 0%;
  z-index: 10;
  background-color: transparent;

  @media screen and (min-width: 1200px) {
    position:absolute;
top: -50%;
left: 0;
  }
}
.products__icon {
  width: 15px;
  height: 15px;
  fill: #d41443;
}
.card-contents__icon {
  margin-bottom: 32px;
}
.card-contents__subtitle {
  padding-top: 160px;

  margin-bottom: 32px;

  text-align: center;
  font-family: 'Titan One';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 1.15;
  text-transform: uppercase;
  color: var(--white-text-color);


  @media screen and (min-width: 768px) {
    padding-top: 118px;
  }
  @media screen and (min-width: 1200px) {
    padding-top: 165px;

}}
.card-contents__text {
  margin-bottom: 32px;
  color: var(--white-text-color);
font-weight: 700;
  line-height: 1.5;
  letter-spacing: 0.04em;
  @media screen and (min-width: 1200px) {
    font-size: 16px;
margin-bottom: 60px;

}}
.card-contents__text::before {
  display: block;
  content: '';
  background-image: url(../images/products_img/group.svg);
  background-repeat: no-repeat;
  height: 6px;
  background-position: center;
  margin-bottom: 32px;
}

.products__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  background-color: var(--white-text-color);
  border-radius: 50%;
  border: transparent;
  width: 40px;
  height: 40px;
}